import { Injectable } from "@angular/core";
import { AmlichModel } from "../models/amlich.model";

declare var jdn;
declare var getCanChi;
declare var TIETKHI;
declare var getGioHoangDao;
declare var getSunLongitude;
declare var LunarDate;
declare var getCanHour0;
declare var CHI;
declare var CAN;
declare var TUAN;
declare var getLunarDate;

@Injectable()
export class AmlichService {

   constructor() {

   }

   getAmlich(dd,mm,yy){
     let amlich = new AmlichModel();
     var lunar_day = getLunarDate(dd,mm,yy);

     amlich.day = lunar_day.day;
     amlich.month = lunar_day.month;
     amlich.year = lunar_day.year;
     amlich.leap = lunar_day.leap;
     amlich.jd = lunar_day.jd;
     var jd = amlich.jd;
     amlich.canchi = getCanChi(lunar_day);
     amlich.tietkhi = TIETKHI[getSunLongitude(jd+1, 7.0)];
     amlich.GioHoangDao = getGioHoangDao(jd);
     amlich.GioDauNgay = getCanHour0(jd)+" "+CHI[0];

     amlich.DayOfWeek = TUAN[(lunar_day.jd + 1) % 7];

     amlich.LunaDayName = amlich.canchi[0];
     amlich.LunaMonthName = amlich.canchi[1];
     amlich.LunaYearName = amlich.canchi[2];

     // console.log(amlich);

     return amlich;
   }

   getCanChiGio(Std,dd,mm,yy,jd){
    var chiGio = (Std >= 23) ? 0 : Math.floor((Std + 1) / 2);
    // var jd = jdn(dd,mm,yy);
    var canGio = ((jd-1)*2 + Math.floor((Std + 1) / 2)) % 10;   
    var gio = CAN[canGio]+' '+CHI[chiGio];
    // console.log(gio);
    return gio;
   }

   getEventMainOfLunaDay(dd,mm){
    var amLichEvents =[
      {day:1,month:1,info:'Tt Nguyn n'},
      {day:15,month:1,info:'Tt Nguyn Tiu (L Thng Nguyn)'},
      {day:3,month:3,info:'Tt Hn Thc'},
      {day:10,month:3,info:'Gi T Hng Vng'},
      {day:15,month:4,info:'L Pht n'},
      {day:5,month:5,info:'Tt oan Ng'},
      {day:15,month:7,info:'L Vu Lan'},
      {day:15,month:8,info:'Tt Trung Thu'},
      {day:9,month:9,info:'Tt Trng Cu'},
      {day:10,month:10,info:'Tt Thng Tn'},
      {day:15,month:11,info:'Tt H Nguyn'},
      {day:23,month:12,info:'Tin To Qun v tri'},
    ];
    return this.findEvents(dd, mm, amLichEvents);        
   }

   getEventMainOfDay(dd,mm){
    var duongLichEvents =[
      {day:1,month:1,info:'Tt Dng Lch'},
      {day:14,month:2,info:'L tnh nhn (Valentine)'},
      {day:27,month:2,info:'Ngy thy thuc Vit Nam'},
      {day:8,month:3,info:'Ngy Quc t Ph n'},
      {day:26,month:3,info:'Ngy thnh lp on TNCS H Ch Minh'},
      {day:1,month:4,info:'Ngy C thng T'},
      {day:30,month:4,info:'Ngy gii phng min Nam'},
      {day:1,month:5,info:'Ngy Quc t Lao ng'},
      {day:7,month:5,info:'Ngy chin thng in Bin Ph'},
      {day:13,month:5,info:'Ngy ca m'},
      {day:19,month:5,info:'Ngy sinh ch tch H Ch Minh'},
      {day:1,month:6,info:'Ngy Quc t thiu nhi'},
      {day:17,month:6,info:'Ngy ca cha'},
      {day:21,month:6,info:'Ngy bo ch Vit Nam'},
      {day:28,month:6,info:'Ngy gia nh Vit Nam'},
      {day:11,month:7,info:'Ngy dn s th gii'},
      {day:27,month:7,info:'Ngy Thng binh lit s'},
      {day:28,month:7,info:'Ngy thnh lp cng on Vit Nam'},
      {day:19,month:8,info:'Ngy tng khi ngha'},
      {day:2,month:9,info:'Ngy Quc Khnh'},
      {day:10,month:9,info:'Ngy thnh lp Mt trn T quc Vit Nam'},
      {day:1,month:10,info:'Ngy quc t ngi cao tui'},
      {day:10,month:10,info:'Ngy gii phng th '},
      {day:13,month:10,info:'Ngy doanh nhn Vit Nam'},
      {day:20,month:10,info:'Ngy Ph n Vit Nam'},
      {day:31,month:10,info:'Ngy Hallowen'},
      {day:9,month:11,info:'Ngy php lut Vit Nam'},
      {day:20,month:11,info:'Ngy Nh gio Vit Nam'},
      {day:23,month:11,info:'Ngy thnh lp Hi ch thp  Vit Nam'},
      {day:1,month:12,info:'Ngy th gii phng chng AIDS'},
      {day:19,month:12,info:'Ngy ton quc khng chin'},
      {day:22,month:12,info:'Ngy l Ging sinh'},
      {day:24,month:12,info:'Ngy thnh lp qun i nhn dn Vit Nam'},
    ];
    return this.findEvents(dd, mm, duongLichEvents);        
   }   

  findEvents(dd, mm, events) {
    var ret = new Array();
    for (var i = 0; i < events.length; i++) {
      var evt = events[i];
      if (evt.day == dd && evt.month == mm) {
        ret.push(evt);
      }
    }
    return ret;
  }   

 }



